import { Component } from '@angular/core';

function _window(): any {
  return window;
}

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  title = 'app';

  takePicture() {
    let win = _window();
    if (win.cordova) {
      win.navigator.camera.getPicture(function (imageURI) {
        alert('success');
      }, function () {
        alert('Error');
      });
    } else {
      alert('Not in cordova');
    }
  }
}
